import React from 'react';
import { Row, Col } from 'antd';
import { createCard3, createCard } from '../common';
import { 
  navigations, indexs, infos, evaluations, 
  exchanges, tools, dapps, knowledge, browsers, wallets, invest
} from '../../data/dashboard/home';

const HomePage = () => (
  <Row gutter={24}>
    <Col xl={15} lg={24} md={24} sm={24} xs={24}>
      {createCard('', navigations)}
      {createCard('', indexs)}
      {createCard(' & ', infos)}
      {createCard('', evaluations)}
      {
        createCard('', exchanges, '20%',
          <span>
            <a href="https://www.feixiaohao.com/exchange/notice" target="_blank" rel="noopener noreferrer">&nbsp;|&nbsp;</a>
            <a href="https://block.cc/notice" target="_blank" rel="noopener noreferrer"></a>
          </span>)
      }
      {createCard('', invest)}
    </Col>

    <Col xl={9} lg={24} md={24} sm={24} xs={24}>
      {createCard3('', knowledge)}
      {createCard3('', tools)}
      {createCard3('BTC | ETH | EOS', browsers)}
      {createCard3('DAPP', dapps)}
      {createCard3('', wallets)}
    </Col>
  </Row>
)

export default HomePage;