import React from 'react';
import { Row, Col } from 'antd';
import { createCard, createCard3 } from '../common';
import { infos, docs, developers, storage } from '../../data/projects/ipfs'

const IpfsPage = () => (
  <Row gutter={24}>
    <Col xl={17} lg={24} md={24} sm={24} xs={24}>
      {createCard('IPFS & ', infos)}
      {createCard('IPFS', docs)}
      {createCard('IPFS', developers)} 
    </Col>

    <Col xl={7} lg={24} md={24} sm={24} xs={24}>
      {createCard3('', storage)}
    </Col>
  </Row>
)

export default IpfsPage;